import React from "react";
import classes from './Post.module.css'

type PostPropsType = {
    message: string;
    count: number;

}

const Post = (props: PostPropsType) => {
    return (
        <div className={classes.item}>
            <img
                src={'data:image/jpeg;base64,'}/>
            {props.message}
            <div>
                <button>{props.count} Like</button>
            </div>

        </div>
    )
}

export default Post;